.services article::before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0) 50%, white 100%);
}

body {
  background: #FFF;
}

img {
  width: 100%;
}

.wrapper {
  max-width: 1280px;
  margin: 0 auto;
}

.row {
  margin-right: 0;
  margin-left: 0;
}

.col-xs-12 {
  padding: 0;
}

.content-wrapper {
  max-width: 900px;
  margin: 0 auto;
}

.parallax {
  position: relative;
  background-attachment: fixed;
  background-position: center 0;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100%;
}

.hidden {
  display: none;
}

body {
  font-family: 'Merriweather', serif;
}

h1 {
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  font-size: 45px;
  text-transform: uppercase;
  color: #FFF;
  text-shadow: 2px 2px rgba(0, 0, 0, 0.5);
}

h2 {
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  font-size: 40px;
}

h3 {
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  font-size: 35px;
  color: #FFF;
  text-shadow: 2px 2px rgba(0, 0, 0, 0.5);
  text-transform: uppercase;
}

h4 {
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 30px;
}

h5 {
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 17px;
  text-transform: uppercase;
}

p {
  font-size: 17px;
  font-weight: 300;
  line-height: 2em;
  margin-bottom: 1.5em;
}

a {
  color: #EFEFEF;
}

ul li {
  font-size: 17px;
  font-family: "Merriweather", serif;
  font-weight: 300;
  line-height: 2em;
}

/** Media alignment */

.alignnone {
  margin-left: 0;
  margin-right: 0;
  max-width: 100%;
  height: auto;
}

.aligncenter {
  display: block;
  margin: 1rem auto;
  height: auto;
}

.alignleft,
.alignright {
  margin-bottom: 1rem;
  height: auto;
}

@media (min-width: 30rem) {
  .alignleft {
    float: left;
    margin-right: 1rem;
  }

  .alignright {
    float: right;
    margin-left: 1rem;
  }
}

/** Captions */

/** Text meant only for screen readers */

.screen-reader-text {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
  color: #000;
  background: #FFF;
}

.btn,
.btn-alt,
#buy-now-drawer .buynow button,
.buynow button {
  padding: 10px;
  background: #000;
  color: #FFF;
  text-decoration: none;
  text-transform: capitalize;
  font-family: "Montserrat", sans-serif;
  font-weight: 300;
  display: block;
  width: 150px;
  text-align: center;
  margin: 1em auto 2em auto;
  border: 2px solid #000;
  cursor: pointer;
}

.btn:hover,
.btn-alt:hover,
#buy-now-drawer .buynow button:hover,
.buynow button:hover {
  background: #FFF;
  color: #000;
}

.btn-alt,
#buy-now-drawer .buynow button {
  background: #FFF;
  color: #000;
  width: auto;
}

.btn-alt:hover,
#buy-now-drawer .buynow button:hover {
  border-color: #FFF;
  color: #FFF;
  background: transparent;
}

#buy-now-drawer {
  display: block;
  position: absolute;
  z-index: 20;
  top: 0;
  width: 100%;
  height: auto;
  max-height: 0;
  overflow: hidden;
  box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.6);
  transition: max-height 0.5s ease;
}

#buy-now-drawer.open {
  max-height: 20rem;
}

#buy-now-drawer .buynow {
  background: #1B2F3F;
  color: #FFF;
}

#buy-now-drawer .buynow .zipcode input {
  color: white;
}

.buynow {
  display: flex;
  padding: 1.5em 4em;
  justify-content: space-between;
  align-items: center;
  text-align: center;
  width: 100%;
  box-sizing: border-box;
  overflow: hidden;
}

.buynow p {
  margin-bottom: 0.5em;
}

.buynow .zipcode {
  position: relative;
  left: 50%;
  transform: translateX(-50%);
}

.buynow .zipcode input {
  background: transparent;
  border: 0;
  border-bottom: 2px solid #858585;
  text-align: center;
  line-height: 36px;
}

.buynow .zipcode input:focus {
  outline: none;
}

.buynow .hidden-label {
  position: absolute;
  left: -10000px;
  top: auto;
}

.buynow .passes {
  position: relative;
  left: 110%;
  display: flex;
  justify-content: space-around;
}

.buynow .passes article {
  width: 50%;
  background: #FFF;
  color: #000;
  margin: 0 20px;
  padding: 10px;
}

.buynow .active {
  transition: 1s;
  transform: none;
  left: 0;
  flex-grow: 2;
}

.banner {
  background: #1B2F3F;
  color: #FFF;
  font-size: 30px;
  font-weight: 400;
  padding: 20px;
  text-align: center;
}

.error {
  color: #B90A0A;
}

.unity-link {
  display: inline-block;
  max-width: 120px;
  max-height: 23px;
  float: right;
}

.svg-unity-logo {
  width: 100%;
  height: auto;
  max-height: 23px;
  display: inline-block;
  vertical-align: middle;
}

.svg-unity-logo path {
  fill: #000;
}

.map-container {
  background: url("../images/map-bg.png");
  background-size: cover;
  background-position: center;
}

#map {
  margin: 0 auto;
  display: block;
}

#map path {
  fill: #1B2F3F;
}

#map path:hover {
  fill: #858585;
}

.tooltip {
  background: #FFF;
  display: none;
  position: absolute;
  font-family: "Montserrat", sans-serif;
  text-align: center;
  padding: 10px;
}

.tooltip .state {
  font-size: 16px;
  font-weight: 500;
}

.tooltip .variation .price {
  font-size: 25px;
  font-weight: 500;
}

.tooltip .variation .duration {
  font-size: 14px;
  font-weight: 500;
}

.tooltip .variation .buy a {
  width: 75px;
  margin: 20px;
}

.not-available {
  width: 300px;
}

.addskier-container {
  display: flex;
  flex-wrap: wrap;
}

.addskier-container .skier-container {
  display: flex;
  position: relative;
  background: #5D7C94;
  color: #FFF;
  margin: 1em 1em 0 0;
  border: 0;
  padding: 1.5em;
}

.addskier-container .skier-container legend {
  position: relative;
  top: 1em;
  font-size: 30px;
}

.addskier-container .skier-container label {
  padding: 0.8em 0;
}

.addskier-container .skier-container input {
  width: 98%;
}

#add-skier {
  align-self: center;
  height: 50px;
  width: 50px;
  border-radius: 50%;
  background: #5D7C94;
  color: white;
  cursor: pointer;
  transition: background 1s, transform 1.5s;
}

#add-skier:hover {
  background: #1B2F3F;
}

#add-skier:focus {
  outline: none;
}

.team .teammember {
  height: 500px;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  position: relative;
  text-align: center;
  overflow: hidden;
}

.team .teammember .teammember-name {
  position: absolute;
  background: white;
  top: 15px;
  padding: 0 30px;
  cursor: pointer;
}

.team .teammember .teammember-name:hover + .teammember-content {
  right: 0;
}

.team .teammember .teammember-content {
  font-size: 1em;
  transition: all 0.5s;
  padding: 20px;
  pointer-events: none;
  height: 100%;
  width: 100%;
  box-sizing: border-box;
  position: absolute;
  background: white;
  right: 100%;
}

.team .teammember .teammember-content p {
  font-size: 14px;
}

#menu-social-menu li a {
  display: block;
  position: relative;
  height: 40px;
}

#menu-social-menu li a::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 40px;
  height: 40px;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}

#menu-social-menu li.facebook-footer a::before {
  background-image: url("../images/facebook.svg");
}

#menu-social-menu li.instagram-footer a::before {
  background-image: url("../images/instagram.svg");
}

#menu-social-menu li.twitter-footer a::before {
  background-image: url("../images/twitter.svg");
}

.logo {
  width: 200px;
}

nav li {
  display: inline-block;
  margin-left: 30px;
  text-transform: uppercase;
  font-family: 'Muli', sans-serif;
  font-size: 17px;
}

nav li a {
  color: #FFF;
  text-decoration: none;
  text-shadow: 2px 2px rgba(0, 0, 0, 0.5);
}

nav li a:hover {
  color: #000;
  text-shadow: none;
}

.buynow-btn a {
  color: #000 !important;
  background: #FFF;
  padding: 20px;
  text-shadow: none !important;
}

.buynow-btn a:hover {
  color: #FFF !important;
  background: #000;
}

.nav-trigger {
  display: none;
  text-decoration: none;
  color: #FFF;
  font-size: 30px;
}

.closed::before {
  content: "\00D7";
  color: #000;
  position: absolute;
  font-size: 45px;
  bottom: -7px;
}

header {
  background-size: cover;
  height: 400px;
  text-align: center;
}

header .site-header {
  padding-top: 20px;
}

.custom-logo {
  width: 100px;
  height: auto;
}

.page-header {
  height: 400px;
}

.header-frontpage {
  position: relative;
  height: 100vh;
  background-size: cover;
  text-align: center;
}

.header-frontpage video {
  position: absolute;
  right: 0;
  bottom: 0;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  z-index: -10;
}

.header-frontpage h1 {
  width: 50%;
  margin: 0 auto;
  position: relative;
  top: 35%;
  transform: translateY(-50%);
}

.header-frontpage .banner {
  position: absolute;
  bottom: 0;
  width: 100%;
  box-sizing: border-box;
}

footer {
  background: #1B2F3F;
  position: relative;
}

footer .row {
  align-items: center;
}

footer .footer-img {
  display: block;
  height: 350px;
  object-fit: cover;
  object-position: bottom;
}

footer .footer-copyright {
  padding: 10px;
  background: #FFF;
}

#menu-footer-nav {
  list-style-type: none;
  column-count: 2;
}

#menu-footer-nav li {
  width: auto;
}

#menu-footer-nav li a {
  color: #FFF;
  text-decoration: none;
  font-family: "Montserrat", sans-serif;
  font-weight: 300;
}

#menu-social-menu {
  list-style-type: none;
  float: right;
}

#menu-social-menu li {
  display: inline-block;
}

#menu-social-menu li a {
  overflow: hidden;
  padding: 0;
  text-indent: -100em;
  width: 60px;
}

.services {
  padding-bottom: 30px;
  text-align: center;
}

.services article {
  height: 600px;
  background-size: cover;
  position: relative;
  margin-bottom: 8em;
}

.services article div {
  position: absolute;
  bottom: -100px;
  width: 100%;
}

.services article div h4 {
  margin-top: 10px;
  margin-bottom: 0;
}

.services article div h5 {
  margin-bottom: 0;
}

.callout {
  height: 600px;
  background-size: cover;
  background-position: bottom;
  position: relative;
}

.callout .callout-text {
  position: absolute;
  width: 30%;
  bottom: 30px;
  left: 60px;
  z-index: 10;
  margin-left: 60px;
}

.products {
  height: 600px;
  background-size: cover;
}

.products .products-container {
  width: 40%;
  float: right;
  margin-right: 60px;
}

.products .products-container article {
  float: left;
  width: 45%;
  background: #FFF;
  padding: 20px 10px;
  box-sizing: border-box;
  text-align: center;
  margin-right: 5%;
}

.products .products-container article h2 {
  margin: 10px;
}

.latestposts {
  background: #1B2F3F;
}

.latestposts article {
  display: flex;
  align-items: center;
  color: #FFF;
  padding: 2em;
}

.latestposts article:nth-of-type(-n+2) {
  border-right: 1px solid #979797;
}

.latestposts article .post-title {
  font-family: "Montserrat", sans-serif;
  font-size: 20px;
}

.latestposts article img {
  width: auto;
  margin-right: 1em;
}

.latestposts article a {
  color: #FFF;
  font-family: "Montserrat", sans-serif;
  font-size: 18px;
}

.sticky-cart {
  box-shadow: -5px 0 10px #EFEFEF;
  width: 100%;
  padding: 0;
}

.sticky-cart [id="sticky-cart"] {
  padding: 1rem 1rem 0;
}

.sticky-cart [id="sticky-cart"].fixed {
  width: 25%;
  position: fixed;
  top: 0;
  right: 0;
}

.form-step {
  position: relative;
}

.form-section {
  position: relative;
  margin-bottom: 20px;
  border: 1px solid #1B2F3F;
}

.form-section > legend {
  font-family: "Montserrat", sans-serif;
  font-weight: bold;
  font-size: 1.3em;
  margin-bottom: 0.5em;
}

.form-row {
  position: relative;
  margin: 0 0 rem-calc(20px);
}

ol.form-progress {
  list-style: none;
  display: table;
  margin: 1.5rem 0;
  table-layout: fixed;
  width: 100%;
  padding: 0;
  counter-reset: progressStep;
}

ol.form-progress:focus {
  outline: 0;
}

ol.form-progress:focus li[data-step-current] {
  outline: grey dotted 1px;
}

.claims-sections {
  display: flex;
  flex-wrap: wrap;
}

.claims-sections article {
  flex-grow: 1;
  flex-basis: 0;
  background: #5D7C94;
  color: #EFEFEF;
  padding: 0 2em;
  margin: 1em 2em;
}

@media (max-width: 767px) {
  .header-frontpage h1 {
    width: auto;
  }

  .callout .callout-text {
    width: auto;
    left: 20px;
    margin-left: 10px;
  }

  .services article {
    height: 400px;
  }

  .products {
    height: 700px;
  }

  .products .products-container {
    width: auto;
    margin-right: 0;
  }

  .latestposts article img {
    display: none;
  }

  .latestposts article:nth-of-type(-n+2) {
    border-color: transparent;
  }

  .nav-trigger {
    display: block;
    position: relative;
    text-align: right;
    z-index: 20;
  }

  .nav {
    display: none;
    position: absolute;
    box-sizing: border-box;
    margin: 0;
    padding: 100px 0;
    right: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    background: #FFF;
    z-index: 10;
  }

  .nav li {
    margin-left: 0;
    display: block;
  }

  .nav li a {
    color: #000;
    text-shadow: none;
    display: block;
    padding: 20px;
  }

  #buynow {
    display: block;
    padding: 2em 10px;
  }

  #buynow .passes article {
    margin: 0;
  }

  #buynow .passes article:first-of-type {
    border-right: 2px solid #1B2F3F;
  }

  .buynow a {
    color: #FFF !important;
    background: #000;
  }
}




